aside {
  border-bottom: 3px double #222023;
  border-top: 3px double #222023;
  color: #403f4e;
  line-height: 1.5em;
  padding-bottom: .5em;
  padding-top: .5em;
  width: 100%;
}
aside h3 {
  text-align: center;
}
aside .pull {
  margin-bottom: .5em;
  margin-left: -20%;
  margin-top: .2em;
}
aside .lowlight {
  opacity: .3;
}
aside .lowlight :hover {
  opacity: 1;
}

.opening {
  border-bottom: 3px double #000;
  border-top: 3px double #000;
  font-size: 2em;
  margin-bottom: 10em;
  padding-bottom: 2em;
  padding-top: 2em;
  text-align: center;
}

blockquote {
  border-left: 5px solid #ccc;
  color: #222023;
  font-weight: 100;
  margin-bottom: 1.2em;
  margin-left: 4em;
  margin-right: 4em;
  margin-top: 2em;
}
blockquote p {
  padding-left: 1em;
}

.pullquote {
  border-bottom: 18px solid #000;
  border-top: 18px solid #000;
  font-size: 36px;
  font-weight: 700;
  letter-spacing: -.02em;
  line-height: 38px;
  margin-right: 100px;
  padding: 20px 0;
  position: relative;
  width: 200px;
}
.pullquote p {
  color: #00298a;
  font-weight: 700;
  position: relative;
  text-transform: uppercase;
  z-index: 1;
}
.pullquote p:last-child {
  line-height: 20px;
  padding-top: 2px;
}
.pullquote cite {
  color: #333;
  font-size: 18px;
  font-weight: 400;
}

.margin-notes,
.content-left {
  clear: both;
  display: block;
  float: left;
  font-size: .75em;
  margin: 1em;
  width: 40%;
}
.margin-notes h3,
.content-left h3 {
  text-align: center;
}

.margin-notes-right,
.content-right {
  clear: both;
  display: block;
  float: right;
  font-size: .75em;
  margin: 1em;
  width: 40%;
}
.margin-notes-right h3,
.content-right h3 {
  text-align: center;
}

.margin-notes-right > p:last-of-type,
.content-right > p:last-of-type {
  clear: both;
  float: right;
}

.margin-notes-left > p:last-of-type,
.content-left > p:last-of-type {
  clear: both;
  float: left;
}

.content-right ul,
.content-left ul {
  list-style: none;
}

p {
  font-size: 1em;
  margin-bottom: 1.3em;
}
p + p {
  text-indent: 2em;
}

.first-line {
  font-size: 1.1em;
  text-indent: 0;
  text-transform: uppercase;
}

.first-letter {
  float: left;
  font-size: 7em;
  line-height: .8em;
  margin-bottom: -.1em;
  padding-right: .1em;
}

ul li {
  list-style: square;
}

ol li {
  list-style: decimal;
}

h1 {
  font-size: 3em;
  line-height: 1.05;
}
