  h1 {
    font-size: 2.5em;
    line-height: 1.125;
  }
}
@media screen and (min-width: 20em) and (max-width: 37.5em) and (-webkit-min-device-pixel-ratio: 1) {
  h1 {
    font-size: 2em;
    line-height: 1.25;
  }
}

h2 {
  font-size: 2.25em;
  line-height: 1.25;
}
@media screen and (min-width: 37.5em) and (max-width: 50em) and (-webkit-min-device-pixel-ratio: 1) {
  h2 {
    font-size: 2em;
    line-height: 1.25;
  }
}
@media screen and (min-width: 20em) and (max-width: 37.5em) and (-webkit-min-device-pixel-ratio: 1) {
  h2 {
    font-size: 1.625em;
    line-height: 1.15384615;
  }
}

h3 {
  font-size: 1.75em;
  line-height: 1.25;
}
@media screen and (min-width: 37.5em) and (max-width: 50em) and (-webkit-min-device-pixel-ratio: 1) {
  h3 {
    font-size: 1.5em;
    line-height: 1.25;
  }
}
@media screen and (min-width: 20em) and (max-width: 37.5em) and (-webkit-min-device-pixel-ratio: 1) {
  h3 {
    font-size: 1.375em;
    line-height: 1.13636364;
  }
}

h4 {
  font-size: 1.125em;
  line-height: 1.25;
}
@media screen and (min-width: 37.5em) and (max-width: 50em) and (-webkit-min-device-pixel-ratio: 1) {
  h4 {
    font-size: 1.125em;
    line-height: 1.222222;
  }
}
@media screen and (min-width: 20em) and (max-width: 37.5em) and (-webkit-min-device-pixel-ratio: 1) {
  h4 {
    font-size: 1.125em;
    line-height: 1.111111;
  }
}

h5 {
  font-size: ms(2);
  line-height: 1.25;
}

h6 {
  font-size: ms(1);
  line-height: 1.25;
}

.container {
  width: 100%;
}

@page {
  size: 8.5in 11in;
  margin: 0.5in 1in;
  /* Footnote related attributes */
  @footnote {
    border-top: 1px solid black;
    padding-top: 6pt;
    counter-increment: footnote;
    float: bottom;
    column-span: all;
    height: auto;
  }
}
body[data-type="article"] {
  font-family: notosans-regular, Verdana, sans-serif;
  font-size: 12pt;
  line-height: 1.375;
  orphans: 4;
  widows: 2;
  page: article;
  page-break-before: always;
  page-break-after: always;
}

section[data-type='bibliography'] {
  font-family: notosans-regular, Verdana, sans-serif;
  font-size: 12pt;
  line-height: 1.375;
  orphans: 4;
  widows: 2;
  page: bibliography;
  page-break-before: always;
  page-break-after: always;
}
section[data-type='bibliography'] p {
  text-align: left;
  text-indent: 0 !important;
}

h1, h2, h3, h4, h5, h6,
p, li, dt, dd, figcaption {
  font-family: notosans-regular, Verdana, sans-serif;
}

pre, code {
  font-family: notomono_regular, Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
}

p b,
p strong,
strong, b {
  font-family: notosans-bold, Verdana, sans-serif;
}

em, i,
i, em,
p em, p i {
  font-family: notosans-italic, Verdana, sans-serif;
}

@page article:left {
  @bottom-right {
    content: counter(page);
  }
}
@page article:right {
  @bottom-right {
    content: counter(page);
  }
}
@page bibliography:left {
  @bottom-right {
    content: counter(page);
  }
}
@page bibliography:right {
  @bottom-right {
    content: counter(page);
  }
}
@page article:blank {
  /* Need this to clean up page numbers in titlepage in Prince*/
  @top-center {
    content: "This page is intentionally left blank";
  }
  @bottom-left-corner {
    content: normal;
  }
  @bottom-right-corner {
    content: normal;
  }
}
/* Footnotes */
span.footnote {
  float: footnote;
  display: prince-footnote;
}

::footnote-marker {
  font-family: notosans-regular, verdana, sans-serif;
  content: counter(footnote) ". ";
  list-style-position: inside;
}

::footnote-call {
  content: counter(footnote);
  vertical-align: super;
  font-size: 65%;
}

/* XReferences */
a.xref[href]::after {
  content: " [See page " target-counter(attr(href), page) "]";
}

/* PDF BOOKMARKS */
body[data-type='article'] h1 {
  -ah-bookmark-level: 1;
  -ah-bookmark-state: open;
  -ah-bookmark-label: content();
  prince-bookmark-level: 1;
  prince-bookmark-state: open;
  prince-bookmark-label: content();
}

body[data-type='article'] h2 {
  -ah-bookmark-level: 2;
  -ah-bookmark-state: open;
  -ah-bookmark-label: content();
  prince-bookmark-level: 2;
  prince-bookmark-state: open;
  prince-bookmark-label: content();
}

body[data-type='article'] h3 {
  -ah-bookmark-level: 3;
  -ah-bookmark-state: open;
  -ah-bookmark-label: content();
  prince-bookmark-level: 3;
  prince-bookmark-state: open;
  prince-bookmark-label: content();
}

body[data-type='article'] h4 {
  -ah-bookmark-level: 4;
  prince-bookmark-level: 4;
}

body[data-type='article'] h5 {
  -ah-bookmark-level: 5;
  prince-bookmark-level: 5;
}

body[data-type='article'] h6 {
  -ah-bookmark-level: 6;
  prince-bookmark-level: 6;
}

small,
.font-small {
  font-size: .833em;
}

.justified {
  text-align: justify;
}

pre {
  background-color: #ddd;
  padding: 1em;
  overflow-wrap: break-word;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.footnote {
  font-family: notosans-regular, verdana, sans-serif;
}

figure {
  counter-increment: figure-count;
  margin: 12pt 0;
  max-width: min-content;
}
figure img {
  max-width: auto;
  max-height: auto;
}
figure figcaption {
  font-size: .75rem;
  color: #333333;
}
figure figcaption::before {
  content: "Figure " counter(figure-count) ": ";
  max-width: min-content;
}

/* Informational messages */
.message {
  border: 1px solid black;
  border-radius: 10px;
  display: block;
  padding: .5em;
  margin: 1em 0;
}
.message.info {
  background-color: lightblue;
}
.message.warning {
  background-color: lightyellow;
}
.message.danger {
  background-color: indianred;
}


